/* General Styles */
.App {
  text-align: center;
}

/* Header Styles */
.App-header {
  padding: 20px;
  background-color: #f5f5dc;
}

.title {
  font-size: 6vw;  /* Make the font size responsive */
  text-align: center;
  margin: 0;
}

.marquee {
  color: olive;
  font-size: 5vw;  /* Adjust font size in the marquee for responsiveness */
  margin: 10px 0;
}

/* Make the logo image responsive */
.logo-img {
  max-width: 80%;
  height: auto;
  display: block;
  margin: 20px auto;  /* Center the image */
}

/* Carousel Styles */
.carousel {
  position: relative;
  width: 95%;
  max-width: 800px;
  margin: 30px auto;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.carousel-image {
  width: 100%;
  max-height: 60vh;
  object-fit: contain;
}

.carousel-text h2 {
  font-size: 5vw;
}

.carousel-text p {
  font-size: 4vw;
  padding: 0 10px;
  word-wrap: break-word;
}

/* Mobile responsiveness using media queries */
@media (max-width: 768px) {
  .title {
    font-size: 8vw;
  }
  
  .marquee {
    font-size: 4vw;
  }

  .carousel-text h2 {
    font-size: 6vw;
  }

  .carousel-text p {
    font-size: 5vw;
  }
}
